import { Pipe, PipeTransform } from '@angular/core';


@Pipe({ name: 'evaluatortype' })
export class EvaluatorTypePipe implements PipeTransform {
  
  transform(id: number): string {
    var evaluatorTypes = [
      { id: 1, type: '' },
      { id: 2, type: '' },
      { id: 3, type: '' },
      { id: 4, type: '' },
      { id: 5, type: '' }
    ]
    return evaluatorTypes.filter(x => x.id == id)[0].type;
  }
}

